.Wrapper{
    position: absolute;
    height: 500px;
}

.topbar{
    z-index: 2000;
    background-color: #4c777a;
    width: 100%;
    height: 2rem;
}
.topbar span{
    display: inline-block;
}
.iconWrapper{
    float: right;
    position: relative;
    display: inline-block;
    height: 2rem;
}
.iconWrapper img{
    width: 1.5rem;
    height: auto;
    padding: 0.25rem 0;
    padding-right: 0.5rem;
}

.Content {
    background-color: white;
    width: 100%;
    height: 468px;

}
