.wrapper {
    width: 100%;
    height: 2rem;
    background: rgba(255,255,255,0.7);
    line-height: 2rem;
}
.icon {
    float: left;
    padding: 0 0.5rem;
    margin: 0;
    box-sizing: border-box;
    height: 2rem;
    width: 3rem;
    display: inline-block;
    text-align: center;
}
.time{
    float: right;
    padding: 0 0.5rem;
    margin: 0;
    box-sizing: border-box;
    width: 8rem;
    height: 2rem;
    display: inline-block;
    text-align: center;
}
.content{
    float: left;
    margin: 0 0.25rem;
    box-sizing: border-box;
    display: inline-block;
    height: 2rem;
    width: 7.5rem;
    line-height: 2rem;
    text-align: center;
    border-top: 1.5px solid black;

}
.icon:hover{
    transition: all 0.5s;
    background: rgba(255,255,255,0.5);
}
.content:hover{
    transition: all 0.5s;
    margin: 0;
    padding: 0 0.5rem;
    width: 8rem;
    background: rgba(255,255,255,0.5);
}
.clicked {
    background: rgba(255,255,255,0.5);
}
img {
    z-index: 2000;
    height: 100%;
}