.content{
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: default;
    float: left;
    margin: 0 0.25rem;
    box-sizing: border-box;
    display: inline-block;
    height: 2rem;
    width: 7.5rem;
    line-height: 2rem;
    text-align: center;
    border-top: 1.5px solid black;

}
.content:hover{
    cursor: default;
    transition: all 0.5s;
    margin: 0;
    padding: 0 0.5rem;
    width: 8rem;
    background: rgba(255,255,255,0.5);
}
.clicked {
    transition: all 0.5ms;
    margin: 0;
    padding: 0 0.5rem;
    width: 8rem;
    background: rgba(255,255,255,0.5);
}