.Wrapper {
    position: absolute;
    top: 0px;
    z-index: 9000;
    width: 100%;
    height: 100%;
    background: linear-gradient( to right, #000428, #004e92);
    transition: all 0.5s;
    opacity: 1;
    display: table;
}
.InnerWrapper{
    display: table-cell;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    text-align: center;
    vertical-align: middle;
}
.InnerWrapper img{
    margin-bottom: 0.8rem;
    box-sizing: border-box;
    top: 20%;
    display: inline-block;
    width: 700px;
    height: auto;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
.InnerWrapper .container{
    vertical-align: middle;
    display: inline-block;
    width: 500px;
    height: 2rem;
    background-color: rgba(255,255,255,0);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
    border: 0px;
    font-size: 1rem;
}
.InnerWrapper .username{
    margin-bottom: 0.8rem;
    box-sizing: border-box;
    padding: 0 1rem;
    border: 0px;
    width: 500px;
    height: 2rem;
    background-color: rgba(255,255,255,0);
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}
@media screen and (max-width: 1020px) {
    .InnerWrapper img{
       width: 500px;
    }
    .InnerWrapper .container{
        width: 300px;
    }
    .InnerWrapper .username{
        width: 300px;
    }
}